.modal.show .modal-dialog {
  top: 50%;
  left: 50%;
  transform: rotate(0deg) translate(0%, 0%);
  -webkit-transform: translate(-50%, -50%);
  transition: transform 0.8s ease-out, -webkit-transform 0.8s ease-out;
  padding-bottom: 1px; }

.modal .modal-dialog {
