.habilidades {
  margin-bottom: 23rem;
  margin-top: 23rem;
  position: relative;

  .container {
    padding-top: 4rem;
    .temas {
      display: flex;
      justify-content: space-around;
      align-items: center;

      &.glass-temas {
        width: 100% - 13.45%;
        padding: 2.3rem 1.8rem;
        margin-right: auto;
        margin-left: auto;
      }
      .tema-dark {
        border-radius: 50%;
        cursor: pointer;
        width: 3.8rem;
        height: 3.8rem;
        position: relative;
        &::before {
          content: "nightlight";
          font-size: 3.8rem;
        }
      }
      .tema-Azul {
        border-radius: 50%;
        cursor: pointer;
        width: 3.8rem;
        height: 3.8rem;
        background: $degradadoA1;
      }
      .tema-Pulpura {
        border-radius: 50%;
        cursor: pointer;
        width: 3.8rem;
        height: 3.8rem;
        background: $degradadoP1;
      }
      .tema-Rojo {
        border-radius: 50%;
        cursor: pointer;
        width: 3.8rem;
        height: 3.8rem;
        background: $degradadoR1;
      }
    }
    .habilidad {
      margin-top: 4rem;
      padding-bottom: 4rem;
      width: 100% - 13.45%;
      margin-left: auto;
      margin-right: auto;
      .habilidad-icons {
        display: flex;
        flex-wrap: wrap;
        gap: 2.4rem;
        justify-content: center;
        margin-bottom: 5rem;
        img {
          width: 4.3rem;
          height: 4.8rem;
        }
      }
      p {
        text-align: center;
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
}
.fondo {
  position: absolute;
  z-index: -1;
  width: 100%;
  top: 50%;
  height: 67rem;
  transform: translateY(-50%);
  @include telefono{
     
  }
  picture{
    display: block;
  }
  img,source {    
    width: 100%;
    height: 100%;
    
  }
}
